import React from 'react'

import { useLayoutPlanner } from '../../hooks/useLayoutPlanner'

import Canvas from '../Canvas'

const LayoutPlanner = () => {
  const {
    tmpEdge,
    nodes,
    edges,
    polygons,
    setCursorCoords,
    beginTmpEdge,
    endTmpEdge,
    getEdgeNodes,
    getCursorCoords,
    getPolygonNodes
  } = useLayoutPlanner()

  return (
    <Canvas
      tmpEdge={tmpEdge}
      nodes={nodes}
      edges={edges}
      polygons={polygons}
      setCursorCoords={setCursorCoords}
      beginTmpEdge={beginTmpEdge}
      endTmpEdge={endTmpEdge}
      getEdgeNodes={getEdgeNodes}
      getCursorCoords={getCursorCoords}
      getPolygonNodes={getPolygonNodes}
    />
  )
}

export default LayoutPlanner
