import React from 'react'
import { Group, Line, Circle, Text } from 'react-konva'

import { EDGE_WIDTH, EDGE_COLOR, NODE_RADIUS, FONT_CONFIG } from '../constants'

import {
  getDistanceBetweenPoints,
  getAngleBetweenPoints
} from '../../../helpers'

const Edge = (props) => {
  const { index, nodes } = props

  const [n1, n2] = nodes
  const textWidth = getDistanceBetweenPoints(n1, n2)

  let angle = getAngleBetweenPoints(n1, n2)
  let textCoords = { ...n1 }

  if (Math.abs(angle) > Math.PI / 2) {
    angle += Math.PI
    textCoords = { ...n2 }
  }

  return (
    <Group>
      <Line
        points={[n1.x, n1.y, n2.x, n2.y]}
        strokeWidth={EDGE_WIDTH}
        stroke={EDGE_COLOR}
      />
      {nodes.map(({ x, y }, nodeIndex) => {
        return (
          <Circle
            key={`edge-node-${nodeIndex}`}
            x={x}
            y={y}
            radius={NODE_RADIUS}
            fill={EDGE_COLOR}
          />
        )
      })}
      <Text
        text={index}
        width={textWidth}
        rotation={(angle * 180) / Math.PI}
        {...textCoords}
        {...FONT_CONFIG}
      />
    </Group>
  )
}

export default Edge
