import React from 'react'
import { Group, Line, Circle } from 'react-konva'

import {
  EDGE_WIDTH,
  TMP_EDGE_COLOR_ALLOWED,
  TMP_EDGE_COLOR_NOT_ALLOWED,
  NODE_RADIUS
} from '../constants'

const TmpEdge = (props) => {
  const { nodes, isAllowed } = props

  const [n1, n2] = nodes
  const color = isAllowed ? TMP_EDGE_COLOR_ALLOWED : TMP_EDGE_COLOR_NOT_ALLOWED

  return (
    <Group>
      <Line
        points={[n1.x, n1.y, n2.x, n2.y]}
        strokeWidth={EDGE_WIDTH}
        stroke={color}
      />
      {nodes.map(({ x, y }, nodeIndex) => {
        return (
          <Circle
            key={`edge-node-${nodeIndex}`}
            x={x}
            y={y}
            radius={NODE_RADIUS}
            fill={color}
          />
        )
      })}
    </Group>
  )
}

export default TmpEdge
